import React, { Component } from 'react';
import Splash from './screens/Splash.js';
import Phone from './screens/Phone.js';
import OTP from './screens/OTP.js';
import PIN from './screens/PIN.js';
import FailedVerification from './screens/FailedVerification.js';

export default class App extends Component {
  state = {
    currScreen: 'splash',
    helper: {}
  };

  componentDidMount() {
    this.setState({
      helper: {
        changePage: this.changePage.bind(this)
      }
    });
  }

  changePage(page) {
    this.setState({ currScreen: page });
  }

  render() {
    return (
      <div>
        { this.state.currScreen=='splash' && <Splash helper={this.state.helper} /> }
        { this.state.currScreen=='phone' && <Phone helper={this.state.helper} /> }
        { this.state.currScreen=='otp' && <OTP helper={this.state.helper} /> }
        { this.state.currScreen=='pin' && <PIN helper={this.state.helper} /> }
        { this.state.currScreen=='failed_verification' && <FailedVerification helper={this.state.helper} /> }
      </div>
    );
  }
}
