import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import '../assets/css/global.css';
import '../assets/css/ripple.css';

const otp1Ref = React.createRef();
const otp2Ref = React.createRef();
const otp3Ref = React.createRef();
const otp4Ref = React.createRef();

const DEFAULT_TIMEOUT = 5;

export default class OTP extends Component {
  state = {
    componentMount: false,
    otp1: '',
    otp2: '',
    otp3: '',
    otp4: '',
    timeout: DEFAULT_TIMEOUT,
    resendShown: false,
    phone: '',
    currentTimeout: null
  };

  componentDidMount() {
    console.log("componentDidMount()");
    if (!this.state.componentMount) {
      this.setState({
        componentMount: true
      }, () => {
        var phone = window.localStorage.getItem('phone');
        this.setState({ phone });
        this.runTimeout();
        otp1Ref.current.focus();
      });
    }
  }

  next() {
    var otp = this.state.otp1.trim()
        +this.state.otp2.trim()
        +this.state.otp3.trim()
        +this.state.otp4.trim();
    if (otp.length < 4) {
      alert('Mohon masukkan 4 digit kode OTP.');
      return;
    }
    window.localStorage.setItem('otp', otp);
    this.props.helper.changePage('pin');
  }

  runTimeout() {
    var timeout = setTimeout(() => {
      if (this.state.currentTimeout != null) {
        clearTimeout(this.state.currentTimeout);
        this.setState({ currentTimeout: null });
      }
      if (this.state.timeout > 0) {
        this.setState({
          timeout: this.state.timeout-1
        }, () => {
          this.runTimeout();
        });
      } else {
        this.setState({ resendShown: true });
      }
    }, 1000);
    this.setState({ currentTimeout: timeout });
  }

  getTimeoutString() {
    var h = Math.floor(this.state.timeout / 3600);
    var m = Math.floor(this.state.timeout % 3600 / 60);
    var s = Math.floor(this.state.timeout % 3600 % 60);
    return (''+m).padStart(2, '0')+':'+(''+s).padStart(2, '0');
  }

  render() {
    return (
      <div style={{ width: '100%', height: '100vh', display: 'flex', flexDirection: 'column', backgroundColor: '#118EEA' }}>
        <Helmet>
          <script type="module" src="https://unpkg.com/ionicons@7.1.0/dist/ionicons/ionicons.esm.js"></script>
          <script nomodule src="https://unpkg.com/ionicons@7.1.0/dist/ionicons/ionicons.js"></script>
        </Helmet>
        <div style={{ width: '100%', height: 60, backgroundColor: '#118EEA', display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
          <div style={{ width: 60, height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <ion-icon name="chevron-back" style={{ color: '#ffffff', fontSize: 20 }} />
          </div>
          <div style={{ color: '#ffffff', fontSize: 17 }}>Register Account</div>
          <div style={{ width: 60, height: '100%' }}></div>
        </div>
        <div style={{ backgroundColor: '#ffffff', width: '100%', flex: 1, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
          <div style={{ color: '#000000', fontSize: 15, marginTop: 20 }}><b>Input OTP code</b></div>
          <div style={{ color: '#888888', fontSize: 12, marginTop: 5 }}>Code sent to +62 { this.state.phone } via</div>
          <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', marginTop: 5 }}>
            <img src={require('../assets/images/wa_logo.png')} style={{ width: 15, height: 15, objectFit: 'contain' }} />
            <div style={{ color: '#26BD73', fontSize: 12, marginLeft: 5 }}>WhatsApp</div>
          </div>
          <div style={{ display: 'flex', flexDirection: 'row', marginTop: 20 }}>
            <div style={{ width: 40, height: 40, border: '1px solid #8888884f', borderRadius: 10 }}>
              <input ref={otp1Ref} value={this.state.otp1} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp1: otp }, () => {
                  if (otp.length == 1) {
                    otp2Ref.current.focus();
                  }
                });
              }} type="tel" style={{ width: '100%', height: '100%', fontWeight: 'bold', textAlign: 'center', fontSize: 20, border: 0, outline: 0, backgroundColor: 'transparent' }} />
            </div>
            <div style={{ marginLeft: 10, width: 40, height: 40, border: '1px solid #8888884f', borderRadius: 10 }}>
              <input ref={otp2Ref} value={this.state.otp2} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp2: otp }, () => {
                  if (otp.length == 1) {
                    otp3Ref.current.focus();
                  }
                });
              }} type="tel" style={{ width: '100%', height: '100%', fontWeight: 'bold', textAlign: 'center', fontSize: 20, border: 0, outline: 0, backgroundColor: 'transparent' }} />
            </div>
            <div style={{ marginLeft: 10, width: 40, height: 40, border: '1px solid #8888884f', borderRadius: 10 }}>
              <input ref={otp3Ref} value={this.state.otp3} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp3: otp }, () => {
                  if (otp.length == 1) {
                    otp4Ref.current.focus();
                  }
                });
              }} type="tel" style={{ width: '100%', height: '100%', fontWeight: 'bold', textAlign: 'center', fontSize: 20, border: 0, outline: 0, backgroundColor: 'transparent' }} />
            </div>
            <div style={{ marginLeft: 10, width: 40, height: 40, border: '1px solid #8888884f', borderRadius: 10 }}>
              <input ref={otp4Ref} value={this.state.otp4} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp4: otp }, () => {
                  this.next();
                });
              }} type="tel" style={{ width: '100%', height: '100%', fontWeight: 'bold', textAlign: 'center', fontSize: 20, border: 0, outline: 0, backgroundColor: 'transparent' }} />
            </div>
          </div>
          <div style={{ height: 30 }} />
          { !this.state.resendShown && <div style={{ backgroundColor: '#F5F5F5', border: '1px solid #8888884f', borderRadius: 5, display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
            <div style={{ backgroundColor: '#ffffff', borderRadius: 5, paddingLeft: 20, paddingRight: 20, paddingTop: 10, paddingBottom: 10, color: '#8888888F', fontSize: 14 }}>
              RESEND
            </div>
            <div style={{ color: '#000000', fontSize: 15, marginLeft: 10 }}><b>{this.getTimeoutString()}</b></div>
            <div className="loader" style={{ width: 15, height: 15, marginLeft: 5, marginRight: 10 }}></div>
          </div> }
          { this.state.resendShown && <div onClick={() => {
            this.setState({
              resendShown: false,
              timeout: DEFAULT_TIMEOUT
            }, () => {
              this.runTimeout();
            });
          }} className="ripple" style={{ cursor: 'pointer', backgroundColor: '#ffffff', color: '#118EEA', border: '1px solid #118EEA', borderRadius: 5, paddingLeft: 20, paddingRight: 20, paddingTop: 10, paddingBottom: 10, fontSize: 14 }}>
            RESEND
          </div>
          }
        </div>
      </div>
    );
  }
}
